import React from 'react';
import Card from './Card';

const CardList = ({ robots }) => {
    return (
        <div>
            {
                robots.map((user, i) => {
                    return  (
                        <Card 
                            key={i} //must add unique id because of virtual DOM parent-child relationship
                            id={robots[i].id} 
                            name={robots[i].name} 
                            email={robots[i].email} 
                         />
                    );
                })
            } 
        </div>
    );
}

export default CardList;