import React from 'react';

const Card = ({ name, email, id }) => { //one way destructuring instead passing (props)
    //const { name, email, id } = props; //one way destructuring instead props.name
    return (
        <div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5'>
            <img alt='robots' src={`https://robohash.org/${id}?200x200`} /> {/*props.name*/}
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
            </div>
        </div>
    );
}

export default Card;
